@-webkit-keyframes Block_shake__XqOu6 {  
    0%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    64%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    70%,80%,90%{
        -webkit-transform: translate(20px);
                transform: translate(20px);
    }
    65%,75%,85%{
        -webkit-transform: translate(-20px);
                transform: translate(-20px);
    }
    100%{
        -webkit-transform: translate(0);
                transform: translate(0);
        background-color: red;
    } 
}

@keyframes Block_shake__XqOu6 {  
    0%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    64%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    70%,80%,90%{
        -webkit-transform: translate(20px);
                transform: translate(20px);
    }
    65%,75%,85%{
        -webkit-transform: translate(-20px);
                transform: translate(-20px);
    }
    100%{
        -webkit-transform: translate(0);
                transform: translate(0);
        background-color: red;
